<template>
  <div class="container">
    <div v-if="product" class="product">
      <div class="container">
        <div class="breadcrumbs">
          <router-link class="breadcrumbs__item" to="/">Главная</router-link>
          <span class="breadcrumbs__item">Контакты</span>
        </div>
        <h1 class="cards__title title">{{ product.title }}</h1>
      </div>
      <div class="product__wrapper">
        <div class="product__image">
          <img :src="product.image" :alt="product.title" />
        </div>
        <div class="product__info">
          <p class="product__desc">
            {{ product.description }}
          </p>
          <p class="product__price">{{ product.price }} руб.</p>
          <button-favorites :isFullInfoPage="true" :product="product" />
          <button class="btn btn_default" @click="orderProduct">Заказать товар</button>
        </div>
      </div>
    </div>
    <Modal v-show="isModalVisible" @close="closeModal" />
  </div>
</template>

<script>
import ButtonFavorites from "@/components/ButtonFavorites.vue"
import Modal from "@/components/Modal.vue"

import { mapGetters, mapMutations } from "vuex"
export default {
  components: { ButtonFavorites, Modal },
  name: "CardProductView",
  data() {
    return {
      id: +this.$route.params.id,
      isModalVisible: false,
    }
  },

  computed: {
    ...mapGetters({
      products: "getProducts",
      getProductById: "getProductById",
    }),

    ...mapMutations(["buyProduct"]),

    product() {
      return this.getProductById(this.id)
    },
  },

  methods: {
    orderProduct() {
      this.isModalVisible = true
      this.$store.commit("buyProduct")
    },
    closeModal() {
      this.isModalVisible = false
    },
  },
}
</script>

