<template>
  <div v-if="isFullInfoPage">
    <div
      v-if="isFavoritesProduct(product.id)"
      @click="deleteFavorites(product.id)"
      class="btn btn_invert"
      role="button"
    >
      Убрать и корзины
    </div>
    <div v-else @click="addFavorites(product)" class="btn btn_default" role="button">
      <div class="favorites-text">Добавить в корзину</div>
    </div>
  </div>
  <div v-else>
    <div
      role="button"
      class="btn-favorites-small"
      v-if="isFavoritesProduct(product.id)"
      @click="deleteFavorites(product.id)"
    >
      <button v-if="isFavoritesPage" class="btn btn_default">Убрать из корзины</button>
      <button v-else class="btn btn_invert">Убрать из корзины</button>
    </div>
    <button v-else @click="addFavorites(product)" class="btn btn_default">
      Добавить в корзину
    </button>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex"
export default {
  name: "ButtonFavorites",
  props: {
    isFullInfoPage: {
      type: Boolean,
    },
    isFavoritesPage: {
      type: Boolean,
    },
    product: {
      type: Object,
    },
  },

  data() {
    return {}
  },
  computed: {
    ...mapGetters({
      isFavoritesProduct: "isFavoritesProduct",
    }),
    ...mapMutations(["setFavoritesProduct", "removeFavoritesProduct"]),
  },

  methods: {
    addFavorites(product) {
      this.$store.commit("setFavoritesProduct", product)
    },
    deleteFavorites(id) {
      this.$store.commit("removeFavoritesProduct", id)
    },
  },
}
</script>
